@import url(https://fonts.googleapis.com/css?family=Poppins:200,300, 500, 700i,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-item {
  font-weight: 300;
}

.visa-header-image {
  max-height: 45vh;
}

.header-text {
  font-weight: 300;
  font-size: 1.7rem;
}

.header-search {
  font-weight: 200;
}

/* Search Box Scrollbar Style*/
.suggestionBoxActive {
  height: 200px;
}

.suggestionBoxInActive {
  height: 0px;
}

.suggestionBox {
  overflow: auto;
  scrollbar-color: #6c58cc #f2edeb;
}

.suggestionBox > div {
  padding: 5px;
  font-weight: 300;
  font-size: 0.9rem;
  border-top: 1px solid #a6acec;
  background-color: white;
}

.suggestionBox > div:hover {
  background-color: #6c58cc;
  cursor: pointer;
}

/* Add Filter Button Style*/
.btn-secondary {
  background-color: #6b62fe;
}

.btn-secondary:focus,
.btn-secondary.focus {
  background-color: #6b62fe;
}

/* Add Filter Button Style*/
.btn-outline-dark {
  background-color: #fff;
  border-color: #efefef;
  border-width: 1px;
}

.btn-outline-dark:hover {
  background-color: #f5f5f5;
  color: #494949;
  border-color: #efefef;
}

.btn-outline-dark:not(:disabled):not(.disabled):active,
.btn-outline-dark:not(:disabled):not(.disabled).active,
.show > .btn-outline-dark.dropdown-toggle {
  color: #494949;
  background-color: #efefef;
  border-color: #fff;
}

.btn-outline-dark:not(:disabled):not(.disabled):active:focus,
.btn-outline-dark:not(:disabled):not(.disabled).active:focus,
.show > .btn-outline-dark.dropdown-toggle:focus {
  box-shadow: none;
}

.btn-outline-dark:focus,
.btn-outline-dark.focus {
  box-shadow: none;
}

/* Center Opening Filter Panel */
.filter-panel {
  position: fixed;
  height: 100vh;
  width: 430px;
  overflow-y: scroll;
  background-color: #ffffff;
  top: 20px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  z-index: 1;
}

.filter-options {
  background-color: #fcfcfc;

  margin-left: 5px;
  margin-right: 5px;
  margin-top: 15px;
  border-radius: 7px;
  padding-bottom: 15px;
}
.filter-options-label {
  font-size: 0.9rem;
  font-weight: 700;
  padding: 5px;
}

