.map-panel {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0px;
  left: 0px;
  background-color: #aba6ff;
  z-index: 1;
}

.map-close {
  position: absolute;
  z-index: 1;
}

.btn-close:hover {
  background-color: #ff4742;
  color: #ff4742;
  border-color: #ff4742;
}

.custome-map {
  width: 100%;
  height: 100%;
}

.countryInfoBox {
  overflow: hidden;
  height: 250px;
  margin-top: 15px;
  margin-bottom: 15px;
}

.countryImage {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  background-origin: border-box;
  border-radius: 4px;
}

.countryName {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: white;
  font-weight: 700;
  font-size: 1.7rem;
  text-align: center;
}

.visaStatus {
  position: absolute;
  bottom: 0;
  left: 0;
  font-weight: 800;
  font-size: 0.8rem;
  text-align: center;
  border-radius: 2px;
  padding: 2px;
  margin-left: 20px;
  margin-bottom: 5px;
  color: white;
  background-color: #cccbe682;
}

.result-count {
  background-color: white;
  display: inline-block;
  border-radius: 3px;
  border: 1px sold #efefef;
  padding: 5px;
  cursor: pointer;
}

